import { Component, OnInit } from '@angular/core';
import { CityserviceService } from '../cityservice.service';
import { City } from '../city.model';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { map, tap, filter, scan, retry, catchError, mergeMap } from 'rxjs/operators';
@Component({
  selector: 'app-listcity',
  templateUrl: './listcity.component.html',
  styleUrls: ['./listcity.component.css']
})
export class ListcityComponent implements OnInit {
/* 
  cities:Observable<City[]>;
  filterdCities:Observable<City[]>; */

  cities:City[];
  filterdCities:City[];
   private _searchText : string;
   private loading:boolean=false;
   private loadingBody:boolean=false;

  constructor(private cityservice :CityserviceService,private router: Router,private route: ActivatedRoute) { }
 
  get searchText(): string{
    return this._searchText;
  }

  set searchText(value:string){
    this._searchText=value;
    this.filterdCities=this.filterCity(value);
  }

  filterCity(searchString:string){
    return this.cities.filter(city=>city.city_name.toLowerCase().indexOf(searchString.toLowerCase()) !== -1);
    /* return this.cities
      .pipe(map(
        cities=>cities.filter(city=>city.city_name.toLowerCase().indexOf(searchString.toLowerCase()) !== -1))); */
  }

  ngOnInit() {
    this.loading=true;
   this.cityservice.showCities().subscribe(cities=>
    {
    
      this.cities=cities;
      this.filterdCities=this.cities;
      this.loading=false;
      this.loadingBody=true;
    });
  
  }

  cityDetails(id:number){
  
    this.router.navigate(['/dashboard/city/'+id]);
    }

}
