import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { City } from './city.model';

@Injectable({
  providedIn: 'root'
})
export class CityserviceService {

  private baseUrl = 'http://52.66.247.11/api';
  constructor(private http: HttpClient) {
   }

   createCity(city: Object): Observable<Object>{
    return this.http.post(`${this.baseUrl}/city`, city);
  }

   showCities(): Observable<any> {
   return this.http.get(`${this.baseUrl}/showcity`);
   }

   getCity(id:number):Observable<any> {
    return this.http.get(`${this.baseUrl}/city/${id}`);
   }
  }
