import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild, CanLoad, Route, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { UserService } from './user.service';

@Injectable({
  providedIn: 'root'
})
export class RoleauthgaurdGuard implements CanActivateChild {

  constructor(private router: Router,private userservice: UserService) {
  }

  canActivateChild(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
      const allowedRoles = next.data.allowedRoles;
      const isAuthorized = this.userservice.isRoleAuthorized(allowedRoles);
      if (!isAuthorized) {
        this.router.navigate(['/login']);
      }
      return isAuthorized;
  }

}
