import { Component, OnInit } from '@angular/core';
import { CategoryserviceService } from "../categoryservice.service";
import { Category } from "../category.model";
import { map, tap, filter, scan, retry, catchError, mergeMap } from 'rxjs/operators';

import { Observable, of } from 'rxjs';
import { Router, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-listcategory',
  templateUrl: './listcategory.component.html',
  styleUrls: ['./listcategory.component.css']
})
export class ListcategoryComponent implements OnInit {

  constructor(private categoryservice : CategoryserviceService,private router: Router) { }
  categories: Category[];
  filtercategories :Category[];
  private _searchText : string;
  private loading:boolean=false;
  private loadingBody:boolean=false;
  get searchText() :string{

     return this._searchText;

  }

  set searchText(value:string){
   this._searchText=value;
   this.filtercategories=this.filterCategory(value);
  }

  /* filterCategory(name: String) {
    return this.categories.filter(proj => proj.name === name);
  } */

   filterCategory(searchString: string):Category[]{

    /* return this.categories.filter(proj => proj.name === name); */
     return this.categories.filter(category=>category.category_name.toLowerCase().indexOf(searchString.toLowerCase()) !== -1);



  }

  ngOnInit() {
     this.reloadData();
  }
  reloadData() {
    this.loading=true;
    this.categoryservice.getcategoriesList().subscribe(cats=>{
      this.categories=cats;
	  console.log(cats);
      this.filtercategories=this.categories;
      this.loading=false;
      this.loadingBody=true;
    });
}

  categoryDetails(id: number){

  this.router.navigate(['/dashboard/category/'+id]);
  }

}
