import { Component, OnInit } from '@angular/core';
import { State } from '../state.model';
import { StateserviceService } from '../stateservice.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-addstate',
  templateUrl: './addstate.component.html',
  styleUrls: ['./addstate.component.css']
})
export class AddstateComponent implements OnInit {

  state: State =new State();
  constructor(private stateservice: StateserviceService) { }
  submitted = false;

  ngOnInit() {
  }

  onSubmit(){
    this.stateservice.createState(this.state).subscribe(data=>{

      console.log(data);
    },(err: HttpErrorResponse) => {
      console.log(err);
    });

    this.submitted = true;
  }

}
