import { Component, OnInit } from '@angular/core';
import {FormGroup, FormControl, Validators} from '@angular/forms';
import { UserService } from '../user.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-createadmin-user',
  templateUrl: './createadmin-user.component.html',
  styleUrls: ['./createadmin-user.component.css']
})
export class CreateadminUserComponent implements OnInit {

  constructor(private userservice: UserService ) { }
   adminForm: FormGroup;
   ngOnInit() {
    this.adminForm = new FormGroup({
     role_id: new FormControl('',Validators.required),
     FirstName: new FormControl('',Validators.required),
     LastName: new FormControl('',Validators.required),
     email: new FormControl('',[Validators.required,Validators.email]),
     password: new FormControl('',[Validators.required,Validators.minLength(6)]),
     password_confirmation: new FormControl('',Validators.required)
    });
  }

  onSubmit(): void {
   console.log(this.adminForm);
   this.userservice.createUser(this.adminForm.value).subscribe(data=>{
     console.log(data);
     this.adminForm.reset();
   },(err : HttpErrorResponse) => {
      console.error(err.error);
      alert(err.error);
  });
}

}
