import { Component, OnInit } from '@angular/core';
import { StateserviceService } from '../stateservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { State } from '../state.model';

@Component({
  selector: 'app-showstate',
  templateUrl: './showstate.component.html',
  styleUrls: ['./showstate.component.css']
})
export class ShowstateComponent implements OnInit {

  constructor(private stateservice : StateserviceService,private router: Router,private route: ActivatedRoute) { }
  id: number;
  state: State;
  ngOnInit() {

    this.state=new State();
    this.id = this.route.snapshot.params['id'];
    this.stateservice.getState(this.id).subscribe(data=>{
      this.state=data;
      console.log(data);
    });
  }

  list(){
    this.router.navigate(['/dashboard/liststate']);
  }

}
