import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommentsService {

private baseUrl = 'http://52.66.247.11/api/complaints/createComments';
private rUrl= '/complaints/createComments';
constructor(private http: HttpClient) { }

creatComments(comments: Object): Observable<Object>{
      return this.http.post(`${this.baseUrl}`, comments);
    }
	
getCommentsComplaintId(id:number):Observable<any>{
	return this.http.get(`http://52.66.247.11/api/complaints/${id}/comments`);
	
}
}


