import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class ComplaintserviceService {

  private baseUrl = 'http://52.66.247.11/api';
  constructor(private http: HttpClient) { }

  getComplaints():Observable<any>{

    return this.http.get(`${this.baseUrl}/getcomplaints`);
  }

  getComplaint(id:number) :Observable<any>{
    return this.http.get(`${this.baseUrl}/complaint/${id}`);
  }

}
