import { Component, OnInit } from '@angular/core';
import { CityserviceService } from '../cityservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { City } from '../city.model';

@Component({
  selector: 'app-showcity',
  templateUrl: './showcity.component.html',
  styleUrls: ['./showcity.component.css']
})
export class ShowcityComponent implements OnInit {

  constructor(private cityservice : CityserviceService,private router: Router,private route: ActivatedRoute) { }
  id: number;
  city: City;
  ngOnInit() {

    
    this.id = this.route.snapshot.params['id'];
    this.cityservice.getCity(this.id).subscribe(data=>{
      this.city=data;
    }

    );
  }

  list(){
    this.router.navigate(['/dashboard/listcity']);
  }

}
