import { Injectable } from '@angular/core';
import { HttpClient , HttpErrorResponse  ,HttpHeaders } from '@angular/common/http';
import { Router } from "@angular/router";
import { Observable } from 'rxjs';
import * as decode from 'jwt-decode';
@Injectable({
  providedIn: 'root'
})
export class UserService {
  readonly rootUrl='http://52.66.247.11';
  private token:string;
  constructor(private http: HttpClient,private router : Router) { }
  userAuthentication(form){
    /* let input = new FormData();
    // Add your values in here
    input.append('email', username);
    input.append('password',password); */
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type' : 'application/x-www-form-urlencoded'
      })
    };
    return this.http.post(this.rootUrl + `/api/login`, form).subscribe((data: any) => {
      console.log(data);
      console.log(data);
      const tokenPayload = decode(data.token);
      console.log(tokenPayload.role);
      localStorage.setItem('datatoken', data.token);
      localStorage.setItem('userid', data.userid);
      localStorage.setItem('email', data.email);
      localStorage.setItem('rolename', data.rolename);
      this.router.navigate(['dashboard/showprofile']);
      },(err: HttpErrorResponse) => {
          this.router.navigate(['/login']);
          console.log('Error in Login');
        });
      }

  loggedIn(): boolean {
    return !!localStorage.getItem('datatoken');
  }

  loggedOut() {
    localStorage.removeItem('datatoken');
    localStorage.removeItem('user_id');
    localStorage.removeItem('email');
    this.router.navigate(['/login']);
  }

  createUser(user: Object): Observable<any> {
   return this.http.post(`${this.rootUrl}/api/register`, user);
  }

  isRoleAuthorized(allowedRoles: string[]): boolean{
    if ( allowedRoles == null || allowedRoles.length === 0) {
         return true;
    }

    this.token = localStorage.getItem('datatoken');
    const decodeToken = decode(this.token);
    const rolename= localStorage.getItem('rolename');
    if (!decodeToken) {
    console.log('Invalid token');
    return false;
     }

    return allowedRoles.includes(rolename);
  }

}



