import { Component, OnInit } from '@angular/core';
import { ComplaintserviceService } from '../complaintservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Complaint } from '../complaint.model';
import { CommentsService } from '../comments-service.service';
import { Comments } from '../comments.model';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-showcomplaint',
  templateUrl: './showcomplaint.component.html',
  styleUrls: ['./showcomplaint.component.css']
})
export class ShowcomplaintComponent implements OnInit {

constructor(private commentservice:CommentsService,private complaintservice : ComplaintserviceService,private router: Router,private route: ActivatedRoute) { }
  id: number;
  complaint: Complaint;
  comment: Comments=new Comments();
  comments:Comments[];
  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.comment.complaint_id = this.route.snapshot.params['id'];
    this.comment.comment_by=localStorage.getItem('email').toString();
    this.complaintservice.getComplaint(this.id).subscribe(data => {
    this.complaint=data;
	  console.log(this.complaint);
    });

	  this.commentservice.getCommentsComplaintId(this.id).subscribe(resp => {
		this.comments = resp.data.comments[0];
		console.log(this.comments);
	});

}
complaintDetails(id: number){
   this.router.navigate(['/dashboard/complaint/'+id]);
    }
 onSubmitComments() {
    this.commentservice.creatComments(this.comment).subscribe(data=>{
		 //this.comments.push(data.comments[0]);
		 this.loadComments();
		 console.log(data);
      },(err: HttpErrorResponse) => {
        console.log(err);
      });

}

loadComments(){
	 this.commentservice.getCommentsComplaintId(this.id).subscribe(resp=>{
		this.comments=resp.data.comments[0];
		console.log(this.comments);
		});
}

}
