import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CanActivateGuard } from './guards/auth-guard.service';
import { AddcategoryComponent } from './addcategory/addcategory.component';
import { AddstateComponent } from './addstate/addstate.component';
import { AddcityComponent } from './addcity/addcity.component';
import { AddwardComponent } from './addward/addward.component';
import { ShowcategoryComponent } from './showcategory/showcategory.component';
import { ListcategoryComponent } from './listcategory/listcategory.component';
import { ListstateComponent } from './liststate/liststate.component';
import { ShowstateComponent } from './showstate/showstate.component';
import { ListcityComponent } from './listcity/listcity.component';
import { ShowcityComponent } from './showcity/showcity.component';
import { ListcomplaintComponent } from './listcomplaint/listcomplaint.component';
import { ShowcomplaintComponent } from './showcomplaint/showcomplaint.component';
import { RoleComponent } from './role/role.component';
import { ListroleComponent } from './listrole/listrole.component';
import { ProfileComponent } from './profile/profile.component';
import { CreateadminUserComponent } from './createadmin-user/createadmin-user.component';
import { CreatesupportUserComponent } from './createsupport-user/createsupport-user.component';
import { CreateofficerUserComponent } from './createofficer-user/createofficer-user.component';
import { RoleauthgaurdGuard } from './roleauthgaurd.guard';
const routes: Routes = [{
  path: 'login',

  component: LoginComponent

},
{
  path: 'register',

  component: RegisterComponent
},
{
  path: 'dashboard',
  component: DashboardComponent, canActivateChild :[RoleauthgaurdGuard],
  children:[
  {path: 'addcategory' , component: AddcategoryComponent,
  data: {
    allowedRoles: ['admin']
  }
  },
  {path: 'addstate' , component: AddstateComponent,
   data: {
    allowedRoles: ['admin']
  }
  },
  {path: 'addcity' , component: AddcityComponent,
  data: {
    allowedRoles: ['admin']
  }
 },
  {path: 'addward' , component: AddwardComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  {path: 'category/:id' , component: ShowcategoryComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'listcategory' , component: ListcategoryComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'liststate' , component: ListstateComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'state/:id' , component: ShowstateComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'listcity' , component: ListcityComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'city/:id' , component: ShowcityComponent,
  data: {
    allowedRoles: ['admin', 'Support']
  }
},
  {path: 'complaint/:id' , component: ShowcomplaintComponent},
  {path: 'listcomplaint' , component: ListcomplaintComponent,
  data: {
    allowedRoles: ['admin', 'Support', 'Officer']
  }
},
  {path: 'addrole' , component: RoleComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  {path: 'showroles' , component: ListroleComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  {path: 'showprofile' , component: ProfileComponent},
  {path: 'createadminuser' , component: CreateadminUserComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  {path: 'createsupportuser' , component: CreatesupportUserComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  {path: 'createofficeruser' , component: CreateofficerUserComponent,
  data: {
    allowedRoles: ['admin']
  }
},
  ],
  canActivate : [CanActivateGuard]
},
{
  path : '',
  redirectTo : '/login' , pathMatch : 'full'
}];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
