import { Component, OnInit } from '@angular/core';
import { CategoryserviceService } from '../categoryservice.service';
import { Category } from '../category.model';
import { Router, ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

@Component({
  selector: 'app-showcategory',
  templateUrl: './showcategory.component.html',
  styleUrls: ['./showcategory.component.css']
})
export class ShowcategoryComponent implements OnInit {

  constructor(private categoryservice : CategoryserviceService,private router: Router,private route: ActivatedRoute) { }
  id: number;
  private loading:boolean=false;
  private loadingBody:boolean=false;
  category: Category;

  ngOnInit() {
    this.loading=true;
    this.category=new Category();
    this.id = this.route.snapshot.params['id'];
    this.categoryservice.getcategory(this.id).subscribe(data=>{
      this.category=data;
      this.loading=false;
      this.loadingBody=true;
    }

    );
  }

  list(){
    this.router.navigate(['/dashboard/listcategory']);
  }

}
