import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Complaint } from '../complaint.model';
import { ComplaintserviceService } from '../complaintservice.service';
import { ActivatedRoute, Router } from '@angular/router';
import { map, tap, filter, scan, retry, catchError, mergeMap } from 'rxjs/operators';

@Component({
  selector: 'app-listcomplaint',
  templateUrl: './listcomplaint.component.html',
  styleUrls: ['./listcomplaint.component.css']
})
export class ListcomplaintComponent implements OnInit {

  complaints :Observable<Complaint[]>;
  filteresComplaint:Observable<Complaint[]>;
  private _searchText: number;
  get searchText():number{
    return this._searchText;
  }

  set searchText(value:number){
    this._searchText=value;
    this.filteresComplaint=this.filterComplaints(value);
  }

  filterComplaints(searchnumber:number){
    return this.complaints
    .pipe(map(
      complaints=>complaints.filter(complaint=>complaint.id.toString().toLowerCase().indexOf(searchnumber.toString().toLowerCase()) !== -1)));

  }
  constructor(private complaintservice : ComplaintserviceService,private router: Router,private route: ActivatedRoute) { }

  ngOnInit() {

    this.complaints=this.complaintservice.getComplaints();
    this.filteresComplaint=this.complaints;
  }

  complaintDetails(id:number){
  
    this.router.navigate(['/dashboard/complaint/'+id]);
    }

}
