import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Profile } from './profile.model';
@Injectable({
  providedIn: 'root'
})
export class ProfileService {
private baseUrl = 'http://52.66.247.11/api';

constructor(private http: HttpClient) { }
getProfile(id:number): Observable<Profile> {
  return this.http.get<Profile>(`${this.baseUrl}/profile/${id}`);
}

updateProfile(id: number, profile: Object): Observable<any>{
  return this.http.post(`${this.baseUrl}/updateProfile/${id}`, profile);
}

}
