import { Component, OnInit } from '@angular/core';
import { StateserviceService } from '../stateservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { State } from '../state.model';
import { HttpErrorResponse } from '@angular/common/http';

import { map, tap, filter, scan, retry, catchError, mergeMap } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-liststate',
  templateUrl: './liststate.component.html',
  styleUrls: ['./liststate.component.css']
})
export class ListstateComponent implements OnInit {

  constructor(private stateservice : StateserviceService,private router: Router,private route: ActivatedRoute) { }
  states: State[];
  filterState:State[];
  private loading:boolean=false;
  private loadingBody:boolean=false;


/*   states: Observable<State[]>;
  filterState:Observable<State[]>; */

  private _searchText:string;
  get searchText():string{
    return this._searchText;
  }

  set searchText(value:string){
    this._searchText=value;
    this.filterState=this.filterstate(value);
  }

    filterstate(searchString:string){
      return this.states.filter(state=>state.state_name.toLowerCase().indexOf(searchString.toLowerCase()) !== -1);
     /*  return this.states
      .pipe(map(
        states=>states.filter(state=>state.state_name.toLowerCase().indexOf(searchString.toLowerCase()) !== -1))); */
    }
   
  ngOnInit() {
   this.loading=true;
    this.stateservice.showStates().subscribe(states=>
      {
        this.states=states;
        this.filterState=this.states;
        this.loading=false;
      });
    
  }

  stateDetails(id:number){
  
    this.router.navigate(['/dashboard/state/'+id]);
    }

}
