import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient , HttpErrorResponse  ,HttpHeaders } from '@angular/common/http';
import { Router } from "@angular/router";
import { State } from './state.model';

@Injectable({
  providedIn: 'root'
})
export class StateserviceService {


  constructor(private http: HttpClient) { }
  private baseUrl = 'http://52.66.247.11/api';

  createState(state:Object):Observable<Object>{
   return this.http.post(`${this.baseUrl}/state`,state);
  }

  showStates():Observable<State[]>{
     return this.http.get<State[]>(`${this.baseUrl}/showstate`);
  }

  getState(id:number) :Observable<any>{
    return this.http.get(`${this.baseUrl}/state/${id}`);
  }
}
