import { Injectable } from '@angular/core';
import { HttpClient , HttpErrorResponse ,HttpHeaders } from '@angular/common/http';
import { Role } from './role.model';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class RoleService {
  private baseUrl = 'http://52.66.247.11/api';
  constructor(private http: HttpClient) { }

  createRole(role:Object):Observable<Object>{
    return this.http.post(`${this.baseUrl}/createrole`,role);
   }

   showRoles():Observable<Role[]>{
    return this.http.get<Role[]>(`${this.baseUrl}/showRoles`);
 }

}
